	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>Message 1493 : Sat Jan 27 (Kandi)      :: Policy Change</p>\n</blockquote>\n<p>Since there are multiple players from the same or similar IPs (home\nnetworks, computer labs, etc.) I would like to inform you of the policy\nchanges for these players.</p>\n<ol>\n<li>You must play on the same side.</li>\n<li>'Boting' will not be allowed. By boting I mean playing via triggers,\netc. (For single players boting is still legal)</li>\n<li>If your friend logs off a darkie, you must wait 20 mins to log your whitie.\nOr vise versa.</li>\n</ol>\n<p>The first two only apply if there is more than one player at the same time.\nThis has always been our policy, but now it's official. Please MUDmail me\nwith any questions you may have.\n-Kandi</p>","frontmatter":{"title":"News 1493: Policy Change","date":"October 09, 2017","author":null,"desc":null}}},"pathContext":{"slug":"/news/1493"}}

